import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({ year, title, summary, poster, genres }) {
    return (
        <div className="movie">
            <img src={poster} alt={title} title={title}></img>
            <div className="movie_data">
                <h3 className="movie_title" style={{ color: "red" }}>{title}</h3>
                <h5 className="movie_year">{year}</h5>
                <ul className="movie_genre">
                    {genres.map((genre, index) => <li key={index} className="genres">{genre}</li>)}
                </ul>
                {/* <p className="movie_summary">{summary.slice(0, 200)}...</p> */}
                <span className="movie_summary">
                    {summary.length > 200
                        ?
                        <p>{summary.slice(0, 200)}...</p>
                        :
                        <p>{summary}</p>
                    }
                </span>
            </div>
        </div>
    );
}

Movie.propTypes = {
    // id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired
};

export default Movie;
